.feedback {
  height: 840px;
  padding-left: 700px;
  background-color: $color-blue;
  background-image: url("../img/gallery/background/feedback@1x.png");
  background-repeat: no-repeat;
  background-position: left bottom;
  box-sizing: border-box;

  @include vp-1365 {
    padding-left: 0;
    background-image: none;
  }

  @include vp-1023 {
    height: 580px;
    min-width: 770px;
    background-image: none;
  }

  @include vp-769 {
    height: 660px;
    min-width: 220px;
  }

  @include retina {
    @include vp-1919 {
      background-image: url("../img/gallery/background/feedback@2x.png");
    }
  }
}

.feedback__container {
  margin: 0 auto;
  width: 655px;
  height: 610px;
  box-sizing: border-box;
  padding: 0;
  padding-top: 170px;
  padding-right: 75px;
  background-color: $color-transparent;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    top: 250px;
    left: -650px;
    width: 510px;
    height: 285px;
    background-image: url("../img/gallery/airplane@1x.png");
  }

  @include vp-1365 {
    padding-right: 0;
    width: 580px;

    &::before {
      display: none;
    }
  }

  @include vp-1023 {
    width: 770px;
    padding: 50px;
    padding-bottom: 125px;
  }

  @include vp-769 {
    width: 100%;
    padding-bottom: 90px;
    padding-top: 50px;
  }

  @include retina {
    &::before {
      background-size: cover;
      background-repeat: no-repeat;
      background-image: url("../img/gallery/airplane@2x.png");
    }
  }
}

.feedback__container h3 {
  font-size: 30px;
  line-height: 42px;
  font-weight: 400;
  color: $color-font-white;
  margin-top: -30px;
}

.feedback__container p {
  font-size: 16px;
  line-height: 22px;
  width: 380px;
  color: $color-font-white;
  margin-top: -20px;
  padding-bottom: 22px;

  @include vp-769 {
    width: 270px;
  }
}

.feedback__fieldset {
  border: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  width: 580px;
  display: grid;
  grid-template-columns: 275px 275px;
  grid-template-rows: repeat(3, min-content);
  column-gap: 30px;
  row-gap: 20px;

  @include vp-1023 {
    width: 670px;
    grid-template-columns: 320px 320px;
  }

  @include vp-769 {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
}

.feedback__fieldset input,
.feedback__fieldset textarea {
  height: 50px;
  width: 100%;
  border: none;
  border-radius: 3px;
  font-family: $ff-placeholder-decorative-regular;
  padding: 15px;
  font-size: 16px;
  line-height: 22px;
  background: $color-input-background;
  cursor: pointer;

  &:hover {
    background-color: $color-very-dark;
  }

  &:focus {
    background-color: $color-card-focus;
    color: $color-font-white;
  }

  &::placeholder{
    user-select: none;
  }
}

.feedback__fieldset textarea {
  height: 155px;
  grid-column: 1/3;
  grid-row: 2/3;
}

.feedback__fieldset button {
  font-family: $ff-placeholder-main-bold;
  color: $color-font-white;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 19px;
  height: 50px;
  margin-bottom: 20px;
  width: 265px;
  border: none;
  border-radius: 3px;
  background-color: $color-very-dark;
  letter-spacing: 1px;
  cursor: pointer;
  user-select: none;

  &:hover {
    background-color: $color-card-background;
    color: $color-default-black;
  }

  &:focus {
    background-color: $color-card-focus;
    color: $color-default-black;
  }

  &:disabled {
    opacity: 0.3;
  }

  @include vp-769 {
    width: 100%;
  }
}

.feedback__fieldset input[type="checkbox"] {
  display: none;
}

.feedback__fieldset label {
  font-size: 14px;
  line-height: 16px;
  font-family: $ff-placeholder-decorative-regular;
  color: $color-font-white;
  padding-left: 23px;
  cursor: pointer;
  user-select: none;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    cursor: pointer;
    width: 18px;
    height: 18px;
    top: 3px;
    left: 0;
    background-color: $color-blue;
    border: 1px solid $color-font-white;
    border-radius: 3px;
    background-image: url("../img/svg/checkbox.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &:hover {
    &::before {
      background-color: $color-very-dark;
    }
  }

  &:focus {
    &::before {
      outline: 1px solid $color-default-black;
      background-color: $color-card-focus;
    }
  }
}

.feedback__fieldset input[type="checkbox"]:not(:checked) + label::before {
  background-image: none;
}
